<template>
  <div id="aboutUsContact">
    <div class="list1">
      <div class="l1-map" id="companyMap">

      </div>
      <div class="l1-info">
        <div>
          <div class="info-1">
            <ul>
              <li><span class="s1"></span><span>4008 831 831</span></li>
              <li><span class="s1"></span><span>corp@archie.com.cn</span></li>
              <li><span class="s1"></span><span>www.archie.cm.cn</span></li>
              <li><span class="s1"></span><span></span></li>
            </ul>
          </div>
          <!--<div class="info-2">-->
            <!--<ul>-->
              <!--<li> http://www.archie.com.cn</li>-->
              <!--<li>Email (#@)</li>-->
              <!--<li>services#archie.com.cn</li>-->
              <!--<li>corp#archie.com.cn</li>-->
            <!--</ul>-->
          <!--</div>-->
        </div>
      </div>
    </div>
    <div class="list2">
      <div class="l2-title">
        <span></span>
      </div>
      <div class="l2-wrap">
        <div class="list-tag">
          <div class="tag-title"></div>
          <ul>
            <li>230263200</li>
            <li>211230263256200</li>
            <li>275200</li>
          </ul>
        </div>
        <div class="list-tag">
          <div class="tag-title"></div>
          <ul>
            <li>211200</li>
            <li>259211230263256200</li>
            <li>236211230263256200</li>
            <li>333211230263256200</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "aboutUsContact",
        data(){
          return{
            map:'',
            marker:''
          }
        },
        mounted:function () {
          var link = document.createElement('link')
          link.rel = 'stylesheet'
          link.href = 'http://cache.amap.com/lbs/static/main1119.css'   // 
          document.body.appendChild(link);
          var script1 = document.createElement('script')
          script1.type = 'text/javascript'
          script1.src = 'http://cache.amap.com/lbs/static/es5.min.js'   // 
          document.body.appendChild(script1);
          var script2 = document.createElement('script')
          script2.type = 'text/javascript'
          script2.src = 'http://webapi.amap.com/maps?v=1.3&key=5433dcc2bc76f4bfae5b9b20179efac5'   // 
          document.body.appendChild(script2);
          var script3 = document.createElement('script')
          script3.type = 'text/javascript'
          script3.src = 'http://cache.amap.com/lbs/static/addToolbar.js'   // 
          document.body.appendChild(script3);
          var _this = this;
          setTimeout(function () {
            _this.map = new AMap.Map('companyMap', {
              resizeEnable: true,
              zoom:19,
              center: [113.049546,23.128268]
            });
            _this.marker = new AMap.Marker({
              position: _this.map.getCenter(),
              draggable: true,
              cursor: 'move',
            });
            // icon: "../../static/img/addressIcon.png"
            _this.marker.setMap(_this.map);
            // 
            _this.marker.setAnimation('AMAP_ANIMATION_BOUNCE');
            _this.marker.setTitle('');
            _this.marker.on('click',function(e){
              marker.markOnAMAP({
                name:'',
                position:_this.marker.getPosition()
              })
            })
          },1000);
        }
    }
</script>

<style scoped>
@import "../../static/css/aboutUsContact.css";
</style>
<style>
  @import "../../static/css/public.css";
</style>



